import './Game.css';
import React, { useEffect, useReducer } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { Helmet } from 'react-helmet';
import { useHistory } from "react-router-dom";

import HowToPlay from '../components/Buttons/HowToPlay'
import StartButton from '../components/Buttons/StartButton';
import DecideButton from '../components/Buttons/DecideButton';
import Darkmode from '../components/Buttons/Darkmode'
import CardList from './CardList'
import Dora from './Dora'
import MyHand from './MyHand'
import DecidedHand from './DecidedHand'
import DecidedField from './DecidedField'
import MyDiscard from './MyDiscard'
import OpponentDiscard from './OpponentDiscard'
import WebSocket from '../components/WebSocket'
import ScoreBoard from '../components/ScoreBoard'
import Lobby from '../components/Lobby'
import WhoseTurn from '../components/WhoseTurn'
import Ron from '../components/Buttons/Ron'
import Result from './Result'
import WaitBackdrop from '../components/WaitBackdrop'
import Footer from '../components/Footer'
import SnackBarGroup from '../components/SnackBarGroup'
import Hint from '../components/Hint'
import Circular from '../components/Circular'
import LogoutButton from '../components/Buttons/Logout-button'

import { userLogin, gameStateMyName } from '../reducer'

const Game = () => {

  const dispatch = useDispatch()
  const history = useHistory()
  const { background } = useSelector(state => state.enableDarkMode)
  const { phase, time, roomID, serverConnected } = useSelector(state => state.gameState)
  const { login, name } = useSelector(state => state.auth)
  const [, forceUpdate] = useReducer(x => x + 1, 0);

  // const API_URL = 'http://localhost:3000'
  const API_URL = "https://intense-brushlands-31556.herokuapp.com"


  useEffect(() => forceUpdate(), [time])
  useEffect(() => {
    const token = window.localStorage.getItem('token')
    if (!token) {
      history.push('/login')
    } else {
      async function authToken() {
        try {
          let res = await fetch(API_URL + '/authByToken', {
            method: 'get',
            headers: {
              'Content-Type': 'application/json',
              'Authorization': token
            }
          })
          res = await res.json()
          if (res.name) {
            console.log(' ', res.name)
            dispatch(userLogin(res.name))
            dispatch(gameStateMyName(res.name))
            
          } else {
            history.push('/login')
            alert(' .  ')
          }
        } catch(e) {
          alert(e)
          history.push('/login')
        }
      }
      authToken()
    }
  }
  , [])


  const renderSwitch = (phase) => {
    switch (phase) {

      case 0: // ,  
        return (
          <div>
            <div className='routeTest'></div>
            {serverConnected 
            ? <h4 style={{color:'blue'}}>.</h4> 
            : <h4 style={{color:'red'}}><Circular /> ..</h4>}
            <Lobby />
            <StartButton />
          </div>
        )
      case 1: // , ,  , 
        return (
          <div className='routeTest'><h4 className='blue'>    </h4>
            <DecideButton />
            <ScoreBoard />
            <div className='field-dora'>
              <CardList />
              <Dora />
            </div>
            <Hint />
            <MyHand />
          </div>
        )
      case 2: //    ,   
        return (
          <div className='routeTest'>
            <ScoreBoard />
            <WhoseTurn /> 
            <div className='Discard-container2'>
              <MyDiscard />
              <OpponentDiscard />
            </div>
            <div className='field-dora'>
              <DecidedField />
              <Dora />
            </div>
              <Hint />
              <Ron />
              <DecidedHand />
          </div>
        )
      default:
        return (
          <div>error</div>
        )
    }
  }


  return (
    <div className="tc">
      <Helmet>
        <meta charSet='utf-8'/>
        <style>{background}</style>
      </Helmet>
      {
        login
        ?
        <div>
          <div className='navbar'>
            <div className = 'tl light-silver'> ID : {' '}{roomID}</div>
            <Darkmode />
            <HowToPlay />
            <LogoutButton />
          </div>
          <h1 className='title'>  17</h1>
          <WaitBackdrop />
          <SnackBarGroup />
          <Result />
          <WebSocket />
          {renderSwitch(phase)}
          <Footer />
        </div>
        : <h1></h1>
      }
      {/* <div className = 'tl light-silver'> ID : {' '}{roomID}</div>
      <h1 className='title'>  17</h1>
      <WaitBackdrop />
      <SnackBarGroup />
      <Result />
      <WebSocket />
      <HowToPlay />
      <Darkmode />
      {renderSwitch(phase)}
      <Footer /> */}
    </div>
  );
}

export default Game;


